import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import axios from 'axios';
import axiosInstance from "./axiosInstance";


export interface ReviewData {
    id: number; // ID 
    owner: {
        id: number; // ID ,   
        first_name: string; //  
        last_name: string; //  
    };
    catnanny: {
        id: number; // ID 
        first_name: string; //  
        last_name: string; //  
    };
    review: string; //  
    rating: number; //   1  10
    created_at: string; //   
}


//     
export const fetchUserReviews = createAsyncThunk(
    'reviews/fetchUserReviews',
    async (_, { rejectWithValue }) => {
        try {
            const token = localStorage.getItem('access_token');
            const response = await axiosInstance.get('/api/reviews/list/', {
                headers: { Authorization: `Bearer ${token}` },
            });
            return response.data; // ,    
        } catch (error: any) {
            return rejectWithValue(error.response ? error.response.data : { error: 'Unexpected error occurred.' });
        }
    }
);

//    
const initialState = {
    reviews: [] as ReviewData[],
    status: 'idle',
    error: null,
};

//    
const reviewsSlice = createSlice({
    name: 'reviews',
    initialState,
    reducers: {},
    extraReducers: (builder) => {
        builder
            .addCase(fetchUserReviews.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(fetchUserReviews.fulfilled, (state, action: any) => {
                state.status = 'succeeded';
                state.reviews = action.payload.results; //    
            })
            .addCase(fetchUserReviews.rejected, (state, action: PayloadAction<any>) => {
                state.status = 'failed';
                state.error = action.payload?.error || 'Failed to load reviews';
            });
    },
});

export default reviewsSlice.reducer;
